import PropTypes from 'prop-types';


export default function Transactions({ transactions }) {
    return (<table class="transaction-history">
  <thead>
    <tr>
      <th>Type</th>
      <th>Amount</th>
      <th>Currency</th>
    </tr>
  </thead>
   <tbody>
        {transactions.map(el => (
     <tr key={el.id}>
      <td>{el.type}</td>
      <td>{el.amount}</td>
      <td>{el.currency}</td>
    </tr> 
    ))} 
   </tbody>
</table>)
}

Transactions.propTypes = {
    transactions: PropTypes.arrayOf(PropTypes.object),
}